.footer{
  padding: 2.31em 0;
  color: #fff;
  background: #091E2D;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__conf{
    color: #fff;
    &:hover{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.44em 0;
  }
}


@media screen and (max-width: 576px){
  .footer{
    text-align: center;
    &__inner{
      flex-direction: column;
    }
    &__c{
      margin-top: .71em;
    }
  }
}
