@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-blue,
.m-btn-red{
    justify-content: unset;
    height: 5.625em;
    border-radius: 0.125em;
    white-space: nowrap;
    .m-btn__icon{
        width: 6.31em;
        height: 100%;
        flex-shrink: 0;
        @include center;
        svg{
            width: 1.125em;
            height: auto;
        }
    }
    .m-btn__inner{
        flex-grow: 1;
        @include center;
        padding: 0 3.875em;
        border-right: 1px solid rgba(255, 255, 255, 0.30);
        height: 100%;
        span{
            font-size: 1.5em;
            line-height: 130%;
        }
    }
}


@media screen and (max-width: 992px){
    .m-btn-red{
        height: 3.75em;
        .m-btn__inner{
            padding: 0;
            span{
                font-size: 1.125em;
            }
        }
        .m-btn__icon{
            width: 3.81em;
            svg{
                width: 1em;
            }
        }
    }
}

.m-btn-red{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,15%);
        color: #fff;
    }
}

.m-btn-blue{
    background-color: $blue;
    color: #fff;
    &:hover{
        background-color: darken($blue,5%);
        color: #fff;
    }
}
