.animated{

}

.main__nav.animated,
.main__title.animated,
.services__title.animated,
.profit__top.animated,
.profit__content.animated,
.experience__top.animated,
.resident__top.animated,
.props__title.animated,
.condition-info__title.animated,
.condition-info__desc.animated,
.condition__title.animated,
.conditions__title.animated,
.territory__title.animated,
.infrastructure__top.animated,
.zone__title.animated,
.location-info__title.animated,
.location-info__item.animated,
.advantage-item.animated{
  transform: translateX(-10em);
  opacity: 0;
  transition: all .5s;
}

.main__nav.animated--active,
.main__title.animated--active,
.services__title.animated--active,
.profit__content.animated--active,
.profit__top.animated--active,
.experience__top.animated--active,
.resident__top.animated--active,
.props__title.animated--active,
.condition-info__title.animated--active,
.condition-info__desc.animated--active,
.condition__title.animated--active,
.conditions__title.animated--active,
.territory__title.animated--active,
.infrastructure__top.animated--active,
.zone__title.animated--active,
.location-info__title.animated--active,
.location-info__item.animated--active,
.advantage-item.animated--active{
  transform: translateX(0em);
  opacity: 1;
}


.main__top.animated,
.profit__nav.animated,
.service-card.animated,
.banks.animated,
.experience-item.animated,
.resident-card.animated,
.condition__col2.animated,
.prop-item.animated,
.territory-slider__img.animated,
.infrastructure-item.animated,
.zone__img.animated,
.zone-prop.animated,
.location__img.animated{
  opacity: 0;
  transition: all .5s;
}

.service-card:nth-child(2),
.experience-item:nth-child(2),
.resident-card:nth-child(2),
.prop-item:nth-child(2),
.territory-slider .swiper-slide-next .territory-slider__img,
.zone-prop._type2{
  transition-delay: .3s;
}

@media screen and (min-width: 577px){
  .service-card:nth-child(3),
  .experience-item:nth-child(3),
  .resident-card:nth-child(3),
  .prop-item:nth-child(3),
  .territory-slider .swiper-slide-next+.swiper-slide .territory-slider__img,
  .zone-prop._type3{
    transition-delay: .5s;
  }
}


@media screen and (min-width: 993px){
  .service-card:nth-child(4),
  .resident-card:nth-child(4),
  .zone-prop._type4{
    transition-delay: .8s;
  }

  .resident-card:nth-child(5){
    transition-delay: 1s;
  }
}


.main__top.animated--active,
.profit__nav.animated--active,
.service-card.animated--active,
.banks.animated--active,
.experience-item.animated--active,
.resident-card.animated--active,
.condition__col2.animated--active,
.prop-item.animated--active,
.territory-slider__img.animated--active,
.infrastructure-item.animated--active,
.zone__img.animated--active,
.zone-prop.animated--active,
.location__img.animated--active{
  opacity: 1;
}
