.mortgage{
  background-color: transparent;
  &__desc{
    display: flex;
    img{
      width: 11em;
      margin-left: .56em;
    }
  }
  .profit__inner{
    align-items: flex-start;
  }
  .profit__content{
    max-width: 47em;
  }
  .profit__nav{
    padding: 8em 0 0;
    display: flex;
  }
}

.banks{
  width: calc(100% - 50.25em);
  margin-left: auto;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3));
}

.bank-item{
  border-right: 1px solid #CBDAE5;
  border-bottom: 1px solid #CBDAE5;
  &:nth-child(3n){
    border-right: none;
  }
  &:nth-last-child(1),
  &:nth-last-child(2),
  &:nth-last-child(3){
    border-bottom: none;
  }
  &__img{
    padding-bottom: 100%;
    position: relative;
    img{
      @include centerAbsolute;
      max-width: calc(100% - 4em);
      max-height: calc(100% - 4em);
      display: block;
    }
  }
}

.territory{
  padding: 1.875em 0;
  overflow: hidden;
}

.territory-slider{
  margin-top: 3.5em;
  .swiper{
    &-container{
      width: calc(100% + .69em);
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: .69em;
    }
    &-pagination{
      display: none;
    }
  }
  &__img{
    display: block;
    @include imgCover(57.1%);
  }
}

.experience{
  padding: 14em 0 19em;
  &__grid{
    margin-top: 6.25em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
  }
}

.experience-item{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__num{
    font-size: 9.375em;
    line-height: 160%;
    font-weight: 500;
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: -4.62em;
    padding: 0 1.25em;
    height: 4.4375em;
    display: flex;
    align-items: center;
    background-color: $blue;
    color: #fff;
    border-bottom: .5em solid $primary;
  }
}

.conditions{
  padding: 1.875em 0 3.5em;
  &__top{
    background-color: $blue;
    color: #fff;
    padding: 1em 0;
  }
  &__items{
    margin-top: 5em;
  }
}

.condition{
  &__inner{
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__col1{
    padding-top: 4.38em;
  }
  &__col2{
    position: relative;
    width: calc(100% + 10em);
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__nav{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 6.25em;
  }
  &__mob{
    display: none;
  }
  @media screen and (min-width: 993px){
    &:nth-child(even){
      .condition{
        &__col1{
          padding-left: 4em;
        }
        &__col2{
          order: -1;
          margin-left: -10em;
        }
      }
    }
  }
}

.condition-info{
  @include grid100gap(1.56em);
}

.props{
  margin-top: 4.25em;
  &__items{
    margin-top: 2.44em;
    max-width: 47em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.prop-item{
  &__icon{
    width: 6.25em;
    height: 6.25em;
    background-color: $blue;
    @include center;
    border-bottom: .1875em solid $primary;
    position: relative;
    &:after,
    &:before{
      content: '';
      position: absolute;
      background-color: $blue;
    }
    &:before{
      width: 0.125em;
      height: 4.25em;
      top: 100%;
      left: 0;
    }
    &:after{
      width: .625em;
      height: .625em;
      top: 100%;
      left: -.25em;
      margin-top: 4.25em;
    }
    img{
      max-width: 3.75em;
      max-height: 3.75em;
      display: block;
    }
  }
  &__info{
    margin-top: 5.625em;
  }
}

.partners{
  overflow: hidden;
  padding: 3.5em 0 6.5em;
  .swiper{
    &-wrapper{
      align-items: center;
    }
    &-slide{
      width: auto;
      padding-right: 2.5em;
    }
  }
  &__img{
    height: 3.75em;
    display: flex;
    align-items: center;
    opacity: .5;
    transition: all .3s;
    &:hover{
      opacity: 1;
    }
    img{
      max-width: 14em;
      max-height: 100%;
    }
  }
}

.resident{
  padding: 3.5em 0;
  &__top{
    display: flex;
    align-items: flex-end;
  }
  &__logo{
    width: 18.9em;
    margin-left: 1em;
  }
  &__grid{
    margin-top: 3.81em;
    display: grid;
    grid-template-columns: repeat(5,20%);
  }
}

.resident-card{
  border: 1px solid $primary;
  border-top: none;
  border-left: none;
  padding: 3.75em 2.5em 1.25em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  @media screen and (min-width: 993px){
    &:nth-child(5n-4){
      border-left: 1px solid $primary;
    }
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5){
      border-top: 1px solid $primary;
    }
  }
  &__icon{
    margin-bottom: 3.3125em;
    height: 6.25em;
    @include center;
    img{
      max-height: 100%;
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .mortgage{
    padding: 1.75em 0 1.25em;
    .profit__inner{
      padding-bottom: 5em;
      position: relative;
    }
    .profit__content{
      margin: 0;
    }
    .profit__nav{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
    }
    .profit__btn{
      width: 100%;
    }
    &__desc{
      img{
        display: none;
      }
    }
  }

  .banks{
    width: 100%;
    margin: 1.25em 0 0;
  }

  .territory-slider{
    margin-top: 1.25em;
    .swiper{
      &-slide{
        width: 50%;
      }
      &-pagination{
        display: flex;
        margin-top: .625em;
      }
    }
  }

  .experience{
    padding: 1.875em 0 2.38em;
    &__grid{
      margin-top: .5em;
    }
  }

  .experience-item{
    &__num{
      font-size: 3.75em;
    }
    &__desc{
      margin-top: -1.88em;
      height: 2.125em;
      border-bottom-width: .375em;
    }
  }

  .conditions{
    padding: .9375em 0;
    &__top{
      padding: .75em 0;
    }
    &__items{
      margin-top: 1.25em;
    }
  }

  .condition{
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
      position: relative;
    }
    &__col1{
      padding: 1.25em 1.25em 6.25em;
      border-bottom: 1px solid #C0D2DF;
    }
    &__col2{
      width: 100%;
      order: -1;
      position: static;
    }
    &__nav{
      bottom: 1.25em;
      width: 100%;
      padding: 0 1.25em;
    }
    &__mob{
      display: block;
      padding: 0 1.25em 1.25em;
    }
  }

  .condition-info{
    .text40{
      display: none;
    }
    .text24{
      line-height: 120%;
    }
  }

  .props{
    margin-top: 1.25em;
    &__items{
      margin-top: 1.25em;
    }
  }

  .prop-item{
    &__icon{
      width: 4.375em;
      height: 4.375em;
      &:before,
      &:after{
        display: none;
      }
      img{
        max-width: 2.9em;
        max-height: 2.9em;
      }
    }
    &__info{
      margin-top: 1em;
    }
  }

  .partners{
    padding: 1.22em 0 1.875em;
    .swiper{
      &-slide{
        padding-right: 1.38em;
      }
      &-pagination{
        margin-top: 1.75em;
      }
    }
    &__img{
      height: 2.68em;
      img{
        max-width: 11.5em;
      }
    }
  }

  .resident{
    padding: .9375em 0 1.22em;
    &__top{
      align-items: flex-start;
      flex-direction: column;
    }
    &__logo{
      width: 12.875em;
      margin: .5em 0 0;
    }
    &__grid{
      margin-top: 1.89em;
      grid-template-columns: repeat(3,calc(100% / 3));
    }
  }

  .resident-card{
    padding: 1.5em 1.5em 1.25em;
    @media screen and (min-width: 577px){
      &:nth-child(3n-2){
        border-left: 1px solid $primary;
      }
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3){
        border-top: 1px solid $primary;
      }
    }
    &__icon{
      height: 4.375em;
      margin-bottom: .45em;
    }
  }
}


@media screen and (max-width: 576px){
  .bank-item{
    &__img{
      img{
        max-width: calc(100% - 2em);
        max-height: calc(100% - 2em);
      }
    }
  }

  .territory-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .experience{
    &__top{
      span{
        font-size: .82em;
      }
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .experience-item{
    @include lastM(-.5em 0 0);
  }

  .props{
    margin-top: 1.25em;
    &__items{
      grid-template-columns: 100%;
      gap: .69em;
    }
  }

  .prop-item{
    display: flex;
    align-items: center;
    &__icon{
      flex-shrink: 0;
    }
    &__info{
      margin: 0 0 0 .875em;
    }
  }

  .resident{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .resident-card{
    border-left: 1px solid $primary;
    &:first-child{
      border-top: 1px solid $primary;
    }
  }
}
