@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper-pagination{
  @include center;
  position: static;
  margin-top: 2.5em;
  &-bullet{
    opacity: 1;
    background-color: $primary;
    margin: 0 .25em !important;
    border-radius: 0;
    width: .75em;
    height: .75em;
    position: relative;
    &:before{
      content: '';
      @include centerAbsolute;
      width: 1em;
      height: 1em;
      border: .125em solid #fff;
      opacity: 0;
      transition: inherit;
    }
    &-active{
      width: 1.25em;
      height: 1.25em;
      &:before{
        opacity: 1;
      }
    }
  }
}
