.experience,
.infrastructure,
.main{
  position: relative;
  color: #fff;
  background-size: cover;
  background-position: center;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background: linear-gradient(180deg, rgba(12, 57, 89, 0.80) 0%, rgba(12, 57, 89, 0.00) 100%), linear-gradient(0deg, rgba(12, 57, 89, 0.30) 0%, rgba(12, 57, 89, 0.30) 100%), linear-gradient(0deg, rgba(0, 0, 0, 0.40) 0%, rgba(0, 0, 0, 0.40) 100%);
  }
  &__inner{
    position: relative;
    z-index: 3;
  }
}

.main{
  &__inner{
    padding: 16.37em 0 17.75em;
    max-width: 56.9em;
  }
  &__top{
    margin-bottom: 2.5em;
  }
  &__logo{
    display: block;
    margin-top: .56em;
    width: 28em;
    max-width: 100%;
  }
  &__nav{
    display: flex;
    margin-top: 5.5em;
  }
}

.location{
  color: $blue;
  &__inner{
    display: grid;
    grid-template-columns: 37em calc(100% - 37em);
  }
  &__content{
    padding: 5em 7em 5em 0;
  }
  &__advantages{
    @include grid100gap(1.625em);
  }
  &__img{
    width: calc(100% + 10em);
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.advantage-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 4.6875em;
    height: 4.6875em;
    background-color: $blue;
    border-bottom: .1875em solid $primary;
    @include center;
    flex-shrink: 0;
    margin-right: 2.2em;
    img{
      width: 3em;
    }
  }
}

.location-info{
  padding-left: 6.88em;
  margin-top: 4em;
  &__items{
    margin-top: 3.2em;
    @include grid100gap(3.7em);
  }
  &__item{
    display: flex;
    align-items: center;
    img{
      width: 2.9em;
      flex-shrink: 0;
      margin-right: 1.78em;
    }
  }
}

.profit{
  padding: 4em 0 5em;
  background: #F7F7F7;
  position: relative;
  &__percent{
    position: absolute;
    opacity: 0.08;
    font-weight: 700;
    top: -6.44em;
    left: -3.69em;
    pointer-events: none;
    span{
      font-size: 25em;
      line-height: 120%;
    }
  }
  &__inner{
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__content{
    max-width: 54.5em;
    margin-right: 2em;
    flex-grow: 1;
  }
  &__top{
    margin-bottom: 3.3125em;
    @include grid100gap(.25em);
  }
  &__desc{
    line-height: 160%;
  }
  &__nav{
    flex-shrink: 0;
    padding-bottom: .56em;
  }
}

.zone{
  padding: 3em 0 1.7em;
  &__inner{
    width: 50.25em;
    margin-left: 23.25em;
    position: relative;
  }
  &__img{
    width: 100%;
    display: block;
    &._view2{
      display: none;
    }
  }
  &__title{
    position: absolute;
    top: 3.91em;
    right: 100%;
    max-width: 24.25em;
    width: 100%;
    margin-right: -1em;
  }
}

.zone-prop{
  position: absolute;
  @media screen and (min-width: 577px){
    &._type1{
      top: 13.22em;
      right: 100%;
      margin-right: .69em;
    }
    &._type2{
      top: 26.42em;
      right: 100%;
      margin-right: 2.37em;
    }
    &._type3{
      top: 13em;
      left: 100%;
      margin-left: 1.19em;
      width: 17em;
    }
    &._type4{
      top: 26.42em;
      left: 100%;
      margin-left: 1.19em;
      width: 17em;
    }
  }
  &__top{
    display: flex;
    white-space: nowrap;
  }
  &__num{
    font-size: 6em;
    line-height: 160%;
    font-weight: 500;
  }
  &__measure{
    padding: 2em 0 0 .5em;
    font-weight: 700;
    line-height: 160%;
  }
  &__desc{
    margin-top: -1.875em;
  }
}

.services{
  padding: 1.875em 0 3.75em;
  &__inner{
    margin-top: 5.31em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
  }
}

.service-card{
  min-height: 24.0625em;
  background: #F7F7F7;
  padding: 6.625em 2.5em;
  position: relative;
  overflow: hidden;
  &:nth-child(even){
    color: #fff;
    background-color: $blue;
  }
  &__img{
    position: absolute;
    bottom: 2.5em;
    right: -2.88em;
    height: 11.87em;
    width: auto;
  }
}

.infrastructure{
  padding: 3.75em 0 10.5em;
  &:after{
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.60) 0%, rgba(0, 0, 0, 0.60) 100%);
  }
  &__top{
    max-width: 20em;
  }
  &__grid{
    margin-top: 10em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2.375em));
    gap: 4.75em;
  }
}

.infrastructure-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 3.75em;
    flex-shrink: 0;
    margin-right: 2.38em;
  }
  &__title{
    span{
      font-weight: 700;
      display: block;
    }
  }
}



@media screen and (max-width: 992px){
  .main{
    &__inner{
      padding: 7.62em 0 4.56em;
    }
    &__top{
      margin-bottom: 1.13em;
    }
    &__logo{
      width: 9.5em;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 17.5em;
    }
  }

  .location{
    padding: 1.875em 0 .625em;
    overflow: hidden;
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      padding: 0 1.25em 1.25em;
    }
    &__img{
      width: 100%;
    }
    &__advantages{
      gap: .625em;
    }
  }

  .advantage-item{
    &__icon{
      width: 3.4375em;
      height: 3.4375em;
      border-bottom-width: 2px;
      margin-right: 1em;
      img{
        width: 2.375em;
      }
    }
  }

  .location-info{
    margin-top: 1.875em;
    padding: 0;
    &__items{
      gap: 1.25em;
      margin-top: 1.25em;
    }
    &__item{
      img{
        margin-right: 1.5em;
      }
    }
  }

  .profit{
    padding: 1.875em 0;
    &__inner{
      display: block;
    }
    &__top{
      gap: .625em;
      margin-bottom: .875em;
    }
    &__content{
      margin: 0 0 1.25em;
    }
    &__desc{
      line-height: 120%;
    }
    &__percent{
      top: -5em;
      span{
        font-size: 18.75em;
      }
    }
  }

  .zone{
    @media screen and (min-width: 577px){
      font-size: .5em;
      &__title{
        max-width: 30.25em;
        right: 88%;
        .text40{
          font-size: 2.7em;
        }
      }
    }
  }

  .services{
    padding: 1.25em 0 1.31em;
    &__inner{
      margin-top: 1em;
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }

  .service-card{
    min-height: 22em;
  }

  .infrastructure{
    padding: 1.875em 0 3.19em;
    &:after{
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.60) 0%, rgba(0, 0, 0, 0.60) 100%);
    }
    &__top{
      max-width: 12em;
    }
    &__grid{
      margin-top: 1em;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }

  .infrastructure-item{
    &__icon{
      margin-right: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    position: relative;
    margin-bottom: 5em;
    &__nav{
      position: absolute;
      left: 0;
      top: 100%;
      width: 100%;
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
    }
  }

  .location{
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .zone{
    padding: 2.5em 0 0;
    .container{
      padding: 0;
    }
    &__inner{
      margin-left: .875em;
      width: calc(100% - .875em);
    }
    &__img{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__title{
      top: -1.25em;
      left: .25em;
    }
  }

  .zone-prop{
    &__num{
      font-size: 2.75em;
    }
    &__measure{
      padding: .5em 0 0 .31em;
      span{
        line-height: 160%;
      }
    }
    &__desc{
      margin-top: -.815em;
    }
    &._type1{
      top: 1.6875em;
      left: .25em;
    }
    &._type2{
      top: 7.69em;
      left: .25em;
    }
    &._type3{
      bottom: 5.85em;
      left: .25em;
      max-width: 8.0625em;
    }
    &._type4{
      bottom: 8.6em;
      left: 9.81em;
      max-width: calc(100% - 9.81em);
    }
  }

  .services{
    padding: 1.25em 0 1.31em;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
  }

  .service-card{
    display: flex;
    align-items: center;
    min-height: 6.6em;
    padding: 1.25em 4.25em 1.25em 1.25em;
    &__img{
      height: 5em;
      top: .81em;
      right: -.25em;
      bottom: unset;
    }
    br{
      display: none;
    }
  }

  .infrastructure{
    &:after{
      background: linear-gradient(180deg, rgba(12, 57, 89, 0.80) 0%, rgba(12, 57, 89, 0.00) 100%), linear-gradient(0deg, rgba(12, 57, 89, 0.30) 0%, rgba(12, 57, 89, 0.30) 100%), linear-gradient(0deg, rgba(0, 0, 0, 0.60) 0%, rgba(0, 0, 0, 0.60) 100%);
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
