.header{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  &__inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: calc(100% + 10em);
  }
  &__logo{
    display: block;
    width: 15.4375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    height: 5.125em;
    font-weight: 700;
    .m-btn__inner{
      span{
        font-size: 1em;
      }
    }
    .m-btn__icon{
      width: 5.125em;
      svg{
        width: 1.875em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    background-color: $blue;
    .container{
      padding: 0 .625em;
    }
    &__inner{
      width: calc(100% + .625em);
    }
    &__logo{
      width: 7.375em;
    }
    &__btn{
      background: transparent;
      height: 2.75em;
      .m-btn__inner{
        padding: 0;
        border: none;
        span{
          font-size: .875em;
        }
      }
      .m-btn__icon{
        width: 2.75em;
        svg{
          width: 1.25em;
        }
      }
    }
  }
}
