.text72{
  font-size: 4.5em;
  line-height: 120%;
}

.text52{
  font-size: 3.25em;
  line-height: 117%;
}

.text40{
  font-size: 2.5em;
  line-height: 117%;
}

.text36{
  font-size: 2.25em;
  line-height: 117%;
}

.text24{
  font-size: 1.5em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 160%;
}

.text14{
  font-size: .875em;
  line-height: 160%;
}

.lh160{
  line-height: 160%;
}

.bold-text{
  font-weight: 700;
}

.primary-text{
  color: $primary;
}

.black-text{
  color: $black;
}


@media screen and (max-width: 992px){
  .text72{
    font-size: 2.25em;
  }

  .text52{
    font-size: 1.5em;
  }

  .text40{
    font-size: 1.375em;
  }

  .text36,
  .text24{
    font-size: 1.125em;
  }
}
